        ShowSearchDataa: async function (search, ths,selctedValue) {             
            var vm = this; 
            var appendingUrl = ``;
            selctedValue.config.options = [];  
            if(selctedValue.name=='account_id'){
                appendingUrl = `id=&&moduleName=CRM&type=ACCOUNTS&search=${search}&refId=`;
            }else if(selctedValue.name=='contact_id'){
                appendingUrl = `id=&&moduleName=CRM&type=ACCOUNT_CONTACTS&search=${search}&refId=`;
            }         
            if (typeof (selctedValue) == 'undefined') {
                selctedValue = 0;
            }           
            await ContractDataService.getDDLData(appendingUrl).then(function (response) {  
                if (response.data) {                    
                    vm.JsonObj = response.data;
                    if (vm.JsonObj != null) {
                        response.data.DATA.forEach((item) => {
                            var obj = {
                                value: item.value,
                                name: item.name
                            }                            
                            selctedValue.config.options.push(obj);                           
                        })
                        vm.$refs.ManageInvoiceForm.UpdateKeyValue();
                    }                    
                }
            });
