<template>

      <div class="my-popups">
            <div class="modal d-block">
                <div class="modal-dialog modal-dialog-centered">
                    <div class="modal-content">
                     
                        <div class="modal-header">
                            <h5 class="modal-title">
                               PriceBook
                            </h5>
                            <button v-on:click="$emit('close')" href="javascript:;" class="close"><em class="fa fa-times" aria-hidden="true"></em></button>
                        </div>
                      
                        <div class="modal-body">
                            <div class="form_content">
                                <section class="main-content no-left-bar">
                                    <div class="container-fluid padding-t_10 p-0" id="Div_OuterForVue" v-cloak>
                                        <div class="right-content">
                                            <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 searchpanel tablehead search float-left p-0">
                                                <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 Search-panel float-left py-2 p-0">
                                                    <div class="col-sm-12 col-md-12 col-lg-6 col-xl-6 float-left">
                                                        <div class="row mt-0">
                                                            <div class="col-6 col-sm-6 col-md-6 col-lg-6 col-xl-6 float-left pr-1 pl-0">
                                                                <div class="form-group">
                                                                    <input type="search" v-model="PriceBookName" class="form-control"
                                                                        v-on:keyup.enter="FetchPriceBookData"
                                                                        :placeholder="$t('Search by')+' '+$t('PriceBook')" />
                                                                </div>
                                                            </div>
                                                            <div class="col-6 col-sm-6 col-md-6 col-lg-6 col-xl-6 float-left pl-0">
                                                                <div class="search-btm-btn background-0 text-left form-check-inline">
                                                                    <a title="" v-on:click="FetchPriceBookData()" href="javascript:;"
                                                                        class="btn btn-success mr-1 px-4" data-toggle-tooltip="tooltip"
                                                                        data-original-title="Search"><em class="fa fa-search"></em></a>
                                                                    <a href="javascript:;" title="" v-on:click="CancelOrRefresh()"
                                                                        class="btn btn-danger clearsearchtext px-4"
                                                                        data-toggle-tooltip="tooltip" data-original-title="Clear"><em                                        class="fa fa-refresh"></em></a>
                                                                </div>
                                                            </div>
                                                        </div>
                                                    </div>
                                                </div>
                                            </div>
                                           
                                            <div class="listing mt-2">
                                             
                                                <div class="table-responsive">
                                                    <table id="example" class="table table-bordered dt-responsive nowrap mt-0"
                                                        style="width:100%" aria-describedby="pricebook">
                                                        <thead class="thead-bg">
                                                            <tr class="text-capitalize">
                                                                 <th scope="col"><span>{{$t('Name')}}</span></th>
                                                                 <th scope="col"><span>{{$t('PricingModel')}}</span></th>
                                                                 <th scope="col"><span>{{$t('ListPrice')}}</span></th>
                                                                 <th scope="col"><span>{{$t('Status')}}</span></th>
                                                            </tr>
                                                        </thead>
                                                        <tbody>
                                                            <tr v-for="(item,itemIndex) in PriceBookData" :key="itemIndex">
                                                                <td><a v-on:click="SetPriceBook(item)" href="javascript:;">
                                                                        {{item.PRICE_BOOK_NAME}}</a></td>
                                                                <td class="position-relative">
                                                                    <span class="float-left">{{item.PRICE_BOOK_MODEL == 1 ? 'Flat':
                                                                        'Differential'}}</span>
                                                                    <a href="javascript:;" class="float-left ml-2 showdiscount"
                                                                        :title="$t('PriceBook')+' '+$t('Discount')"
                                                                        v-on:mouseover="ShowDiscountPricebook(item)"><em                                        class="fa fa-eye text-info" style="font-size:18px;"></em></a>
                                                                    <div class="col-12 float-left p-0" style="display:none;"
                                                                        v-bind:id="'popover-content'+item.PRICE_BOOK_ID">
                                                                        <div class="col-12 float-left listing p-0">
                                                                            <div class="table-responsive tblpricebookdata" style="width:255px;">
                                                                                <table class="table table-bordered mb-0" aria-describedby="range">
                                                                                    <thead class="thead-bg">
                                                                                        <tr>
                                                                                             <th scope="col"><small>{{$t('RangeFrom')}}</small></th>
                                                                                             <th scope="col"><small>{{$t('RangeTo')}}</small></th>
                                                                                             <th scope="col"><small>{{$t('Discount')}}
                                                                                                    {{(item.PRICE_BOOK_MODEL == 1 ? '(Flat)':
                                                                                                    '(%)')}}</small></th>
                                                                                        </tr>
                                                                                    </thead>
                                                                                    <tbody>
                                                                                        <tr v-for="(discountItem,discountndex) in MakeNormalArray(item.PRICE_BOOK_RANGE)"
                                                                                            :key="discountndex">
                                                                                            <td>{{discountItem.FROM_RANGE}}</td>
                                                                                            <td>{{discountItem.TO_RANGE}}</td>
                                                                                            <td>{{$options.filters.currency(discountItem.DISCOUNT)}}</td>
                                                                                        </tr>
                                                                                    </tbody>
                                                                                </table>
                                                                            </div>
                                                                        </div>
                                                                        <div class="clearfix"></div>
                                                                    </div>
                                                                </td>
                                                                <td>{{$options.filters.currency(item.LIST_PRICE)}}</td>
                                                                <td class="text-center"><span
                                                                        v-bind:class="{'px-2 bg-success text-white d-block': (item.STATUS_NAME == 'Active'),'px-2 bg-danger text-white d-block text-center': (item.STATUS_NAME == 'Closed')}">
                                                                        {{item.STATUS_NAME}}</span></td>
                                                            </tr>
                                                            <tr v-if="PriceBookData== null || PriceBookData.length==0">
                                                                <td class="text-center text-danger no-record" colspan="4">
                                                                    {{$t('NoRecordfound')}}</td>
                                                            </tr>
                                                        </tbody>
                                                    </table>
                                                </div>
                                                <!-- <pager-control v-bind:numberofrecords="TotalRecords" :prev-text="$t('Prev')"
                                                    :first-text="$t('First')" :last-text="$t('Last')" :next-text="$t('Next')"
                                                    v-bind:callbackfunction="FetchPriceBookData"></pager-control> -->
                                            </div>
                                          
                                        </div>
                                    </div>
                                </section>
                            </div>
                        </div>
                       
                        <div class="modal-footer d-none">
                            <button type="button" class="btn btn-danger" v-on:click="$emit('close')">Close</button>
                        </div>
                    </div>
                </div>
            </div>
        </div>

</template>

<script>
import DataServices from "../../../services/DataService";
export default {
    props:{
        selectedItemIndex:null,
        summaryData:null
    },
    data() {
            return {
                PriceBookData: null,
                TotalRecords: 0,
                PageNumber: 1,
                PageSize: 10,
                SortBy: '',
                SortExp: '',
                PageSizeOption: null,
                CurrentPage: 1,
                TotalPages: 0,
                GroupStart: 1,
                GroupEnd: 3,
                PriceBookName: '',
                ProductId: 0,
            }
        },
        created() {
            this.FetchPriceBookData();
            this.GetPageSizeOption();
        },
        methods: {
            CancelOrRefresh() {
                this.PriceBookName = '';
                this.FetchPriceBookData();
            },
            GetPageSizeOption: function () {
                var list = [];
                var defaultPageSize = 10;
                for (var i = defaultPageSize; i < defaultPageSize + 95; i += 5) {
                    list.push(i);
                }
                this.PageSizeOption = list;
            },
            SetPageSize: function (event) {
                this.PageSize = event.target.value;
                this.PageNumber = 1;
                this.GroupStart = this.PageNumber;
                this.GroupEnd = this.PageNumber + 2;
                this.FetchData();
            },
            SetCurrentPage: function (pageNumber) {
                this.PageNumber = pageNumber;
                if (pageNumber > this.GroupEnd || pageNumber == 1) {
                    this.GroupStart = pageNumber;
                    this.GroupEnd = pageNumber + 2;
                }
                else if (pageNumber < this.GroupStart && pageNumber > 1) {
                    this.GroupEnd = this.GroupStart - 1;
                    this.GroupStart = pageNumber - 2;
                }
                this.FetchData();
            },
            SetPriceBook: function (item) {
                this.$emit('SetPriceBook',item);               
            },
            ShowDiscountPricebook: function (item) {
                var priceBookId = item.PRICE_BOOK_ID;
                $(".showdiscount").popover("dispose");
                $(".showdiscount").popover({
                    html: true,
                    sanitize: false,
                    content: function () {
                        return $('#popover-content' + priceBookId).html();
                    },
                    placement: "right",
                    template: '<div class="popover my-popover" role="tooltip"><div class="arrow"></div><h3 class="popover-header"></h3><div class="popover-body">' + $('#popover-content' + priceBookId).html() + '</div></div>',
                    trigger: "focus"
                });
            },
            MakeNormalArray: function (value) {
                try {
                    return JSON.parse(value);
                }
                catch (ex) {
                    return value;
                }
            },
            FetchPriceBookData: function () {
               var vm=this;
                var index = vm.selectedItemIndex;
                var selectedItem =vm.summaryData==null?null:vm.summaryData[index];
                if (selectedItem && selectedItem.ProductName != null) {
                    if (Array.isArray(selectedItem.ProductName)) {
                        vm.ProductId = selectedItem.ProductName[0];
                    }
                    else {
                        vm.ProductId = selectedItem.ProductName.value;
                    }
                }                
                var url =`moduleName=${vm.$route.params.moduleName}&subModuleCode=${vm.$route.params.subModuleCode}&pageSize=${vm.PageSize}&pageNumber=${vm.PageNumber}&sortBy=${vm.SortBy}&sortExp=${vm.SortExp}&search=${vm.PriceBookName}&refId=&productId=${vm.ProductId}`;
                vm.PriceBookData = [];
                DataServices.PriceBookProduct(url).then(response => {                             
                              if (response.data !='') {
                                  vm.PriceBookData = response.data.DATA;
                                  vm.TotalRecords = vm.PriceBookData[0].TOTALRECORDS;
                                  vm.TotalPages = Math.ceil(vm.TotalRecords / vm.PageSize);
                                  vm.CurrentPage = vm.PageNumber;
                                  vm.noRecord = false;
                              }
                              else {
                                  vm.PriceBookData = [];
                                  vm.TotalRecords = 0;
                                  vm.TotalPages = 0;
                              }
                              setTimeout(function(){vm.CheckBoxBootstrap()}, 100);                             
                          })
            },
        }
}
</script>